<template>
    <div>
        <br>
        <br>
        <br>
        <div>
            <br>
            <br>
            <p style="color: black;font-size: 24px">!</p>
            <br>
            <p style="color: black;font-size: 18px">,!</p>
        </div>
        <br>
        <br>
        <br>
        <el-row>
            <el-col :span="6" :offset="9">
                <el-input
                        placeholder=""
                        v-model="new_password"
                        prefix-icon="el-icon-key"
                        show-password>
                </el-input>
                <br>
                <br>
                <br>
                <el-input
                        placeholder=""
                        v-model="re_password"
                        prefix-icon="el-icon-key"
                        show-password>
                </el-input>
                <br>
                <br>
                <br>
                <el-button style="width:100%" @click="save_pwd" type="success"> </el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Email_editor_pwd",
        data() {
            return {
                new_password: '',
                re_password: '',
                username: '',
            }
        },
        methods: {
            save_pwd() {
                if ((!this.new_pwd && this.re_pwd)) {
                    this.$message({
                        message: '!',
                        type: 'warning',
                        duration: 1500
                    });
                    return false
                }
                if ((!this.new_pwd === this.re_pwd)) {
                    this.$message({
                        message: ',',
                        type: 'warning',
                        duration: 1500
                    });
                    return false
                }
                // console.log(this.$cookies.get('email_token'))
                this.$http({
                    url: this.$settings.base_url + '/api/student/email_save_pwd/',
                    method: 'post',
                    data: {
                        token: this.$cookies.get('email_token'),
                        new_password: this.new_password,
                        re_password: this.re_password
                    }
                }).then(data => {
                    if (data.data.code === 200) {
                        this.$message({
                            message: data.data.msg,
                            type: 'success',
                            duration: 1500
                        });
                        this.$router.push('/login')
                    } else {
                        this.$message({
                            message: data.data.msg,
                            type: 'error',
                            duration: 1500
                        });
                    }
                })
            }
        }
    }
</script>

