<template>
    <div style="width: 100%">
        <Header></Header>
        <el-row style="margin-top: 30px">
            <el-col :span='6' :offset="2">
                <el-card shadow="hover">
                    <div slot="header">
                        <span></span>
                    </div>
                    <el-row>
                        <el-upload :action="this.$settings.base_url+'/api/student/editor_info/editor_avatar/'"
                                   accept="image/jpeg,image/gif,image/png"
                                   :before-upload="onBeforeUpload"
                                   :on-success="uploadSuccess"
                                   :headers="header"
                                   :show-file-list="false">
                            <el-avatar @click="handlePictureCardPreview(file)" class="el-dropdown-avatar"
                                       :size="100"
                                       :src="this.$settings.base_url +'/media/'+ form.avatar">
                            </el-avatar>
                        </el-upload>
                    </el-row>
                    <el-row style="margin-top: 10px" class="user-info-userName">
                        <label>{{form.username}}</label>
                    </el-row>
                    <el-divider/>
                    <el-row style="text-align: left;position: relative" class="user-info-fullInfo">
                        <p style="position: absolute;top: 0px">{{form.real_name}}</p><br>
                        <p style="position: absolute;top: 24px">{{form.major}}</p><br>
                        <p style="position: absolute;top: 48px">{{form.college}}</p><br>
                        <p style="position: absolute;top: 72px">{{form.create_time}}</p><br>
                        <br>
                        <br>
                        <br>
                        <br>
                    </el-row>
                </el-card>
            </el-col>
            <el-col :span='13' :offset="1">
                <el-card shadow="hover">
                    <el-tabs active-name="event" type="card">
                        <el-tab-pane label="" name="event">
                            <!--                            {{user_dynamics_list}}-->
                            <div class="block" style="position: relative">
                                <el-timeline :reverse="true">
                                    <el-timeline-item
                                            placement="top"
                                            :key="item.id"
                                            v-for="item in user_dynamics_list">
                                        <p style="position: absolute;left: 16px;top: 0px;color: #999999">
                                            {{item.time}}</p>
                                        <el-card style="position: relative">
                                            <br>
                                            <p style="position:absolute;left: 40px">{{item.message}}</p>
                                            <br>
                                            <br>
                                        </el-card>
                                    </el-timeline-item>
                                </el-timeline>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="" name="update">
                            <el-form style="text-align: left" :model="form" label-width="100px" :rules="rules">
                                <el-form-item label="" prop="real_name" required>
                                    <el-input v-model="form.real_name"></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.age"></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-select v-model="form.sex" placeholder="" clearable>
                                        <el-option value=""></el-option>
                                        <el-option value=""></el-option>
                                        <el-option value=""></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-select v-model="form.major" placeholder="" clearable>
                                        <el-option v-for="major in major_list" :value="major.major_name">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.email"></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.phone"></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="save_info"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-tab-pane>
                    </el-tabs>
                </el-card>
                <br>
                <br>
                <br>
                <br>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import Header from "../../components/Home/Header";

    export default {
        data() {
            return {
                header: {
                    token: this.$cookies.get('token')
                },
                user_dynamics_list: [],
                event: [],
                major_list: [],
                form: {
                    avatar: this.$cookies.get('avatar'),
                    real_name: '',
                    username: '',
                    age: '',
                    sex: '',
                    sex_id: '',
                    phone: '',
                    email: '',
                    major: '',
                    major_id: '',
                    create_time: 'null',
                    college: '',
                },
                rules: {
                    real_name: [
                        {required: true, message: '', trigger: 'click'}
                    ],
                }
            }
        },
        created() {
            // 
            this.$http({
                url: this.$settings.base_url + '/api/student/major/',
                method: 'get',
                headers: {
                    token: this.$cookies.get('token')
                }
            }).then(data => {
                this.major_list = data.data
            })

            // 
            this.$http({
                url: this.$settings.base_url + '/api/student/info/' + this.$cookies.get('user_id') + '/',
                method: 'get',
                headers: {
                    token: this.$cookies.get('token')
                }
            }).then(data => {
                this.form.real_name = data.data.real_name
                this.form.username = data.data.username
                this.form.age = data.data.age
                this.form.sex = data.data.get_sex
                this.form.major = data.data.get_major
                this.form.email = data.data.email
                this.form.phone = data.data.phone
                this.form.college = data.data.college
                this.form.create_time = this.$settings.show_time(data.data.create_time)
            })

            // 
            this.$http({
                url: this.$settings.base_url + '/api/student/user_dynamics_list/?user_id=' + this.$cookies.get('user_id'),
                method: 'get',
                headers: {
                    token: this.$cookies.get('token')
                }
            }).then(data => {
                for (let obj of data.data.result) {
                    this.user_dynamics_list.push({
                        time: this.$settings.show_time(obj.create_time),
                        message: obj.message,
                        username: obj.get_user,
                    })
                }
            })
        },

        methods: {
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            uploadSuccess(data, file) {
                if (data.code === 200) {
                    this.$message({
                        message: data.msg,
                        type: 'success',
                        duration: 1500
                    });
                    this.avatar = data.avatar
                    this.$cookies.set('avatar', data.avatar, '7d');
                } else {
                    this.$message({
                        message: data.msg,
                        type: 'error',
                        duration: 1500
                    });
                }
            },
            onBeforeUpload(file) {
                const isIMAGE = file.type === 'image/jpeg' || 'image/gif' || 'image/png';
                const isLt1M = file.size / 1024 / 1024 < 1;
                if (!isIMAGE) {
                    this.$message.error('!');
                }
                if (!isLt1M) {
                    this.$message.error(' 1MB!');
                }
                return isIMAGE && isLt1M;
            },
            save_info() {
                for (let major of this.major_list) {
                    if (this.form.major === major.major_name) {
                        this.form.major_id = major.id
                    }
                }
                if (this.form.sex === '') {
                    this.form.sex_id = '0'
                } else if (this.form.sex === '') {
                    this.form.sex_id = '1'
                } else {
                    this.form.sex_id = '2'
                }

                this.$http({
                    url: this.$settings.base_url + '/api/student/editor_info/' + this.$cookies.get('user_id') + '/',
                    method: 'put',
                    headers: {
                        token: this.$cookies.get('token')
                    },
                    data: {
                        'real_name': this.form.real_name,
                        'age': this.form.age,
                        'sex': this.form.sex_id,
                        'major': this.form.major_id,
                        'email': this.form.email,
                        'phone': this.form.phone,
                    }
                }).then(data => {
                    if (data.data.code === 200) {
                        this.$message({
                            message: data.data.msg,
                            type: 'success',
                            duration: 1500
                        });
                    } else {
                        if (data.data.msg.phone) {
                            this.$message({
                                message: '',
                                type: 'error',
                                duration: 1500
                            });
                        } else if (data.data.msg.email) {
                            this.$message({
                                message: '',
                                type: 'error',
                                duration: 1500
                            });
                        } else {
                            this.$message({
                                message: ',!',
                                type: 'error',
                                duration: 1500
                            });
                        }
                    }
                })
            },
        },
        components: {
            Header,
        }
    }
</script>

<style scoped>
    .el-card {
        margin-top: 30px;
    }
</style>

