<template>
    <div>
        <el-row style="margin-top: 33px">
            <h2></h2>
            <el-col :span="6" :offset="9">
                <el-input
                        placeholder=""
                        prefix-icon="el-icon-user-solid"
                        v-model="username"
                        clearable>
                </el-input>
                <el-row>
                    <el-col :span="14">
                        <el-input
                                placeholder=""
                                prefix-icon="el-icon-s-comment"
                                v-model="code"
                                clearable>
                            <img src="" alt="">
                        </el-input>
                    </el-col>
                    <el-col :span="8">
                        <div style=" color: white;border-radius: 10px;margin-top: 30px">
                            <img ref="img" @click="img_code" height="40" width="160"
                                 :src="this.$settings.base_url+'/api/'+'random_num/'">
                        </div>
                    </el-col>
                </el-row>
                <el-button @click="send_code" type="success"></el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>

    export default {
        name: "Verification",
        data() {
            return {
                username: '',
                code: '',
                sRc: '',
            }
        },
        methods: {
            img_code() {
                this.sRc = this.$refs.img.src + '?'
                this.$refs.img.src = this.sRc
            },

            // 
            send_code() {
                if (!(this.username && this.code)) {
                    this.$message({
                        message: '!',
                        type: 'warning',
                        duration: 1500
                    });
                    return false  // 
                }
                this.$http({
                    url: this.$settings.base_url + '/api/student/find_account/',
                    method: 'post',
                    data: {
                        username: this.username,
                        code: this.code
                    }
                }).then(data => {
                    // console.log(data)
                    if (data.data.code === 200) {
                        this.$emit('method')
                        sessionStorage.email = data.data.result.email;
                        sessionStorage.phone = data.data.result.phone;
                        sessionStorage.username = data.data.result.username;
                    } else{
                        if (data.data.code === 999) {
                            this.$message({
                                type: 'error',
                                message: ',!',
                            });
                        } else {
                            this.$message({
                                type: 'warning',
                                message: data.data.msg
                            });
                            this.sRc = this.$refs.img.src + '?'
                            this.$refs.img.src = this.sRc
                        }
                    }
                })
            }
        },
    }
</script>

<style scoped>
    .el-input, .el-button {
        margin-top: 30px;
    }

    .el-button {
        width: 100%;
        font-size: 18px;
    }
</style>