<template>
    <div>
        <el-row v-if="is_method" style="margin-top: 8px">
            <h2></h2>
            <el-col :span="12" :offset="6">
                <div v-if="is_email" class="div">
                    <div class="img_box">
                        <img src="../../assets/img/email.jpg" class="font_img">
                    </div>
                    <div class="text_box">
                        <h3 style="margin-left: -510px"></h3>
                        <span style="margin-left: -10px;font-size: 14px;">{{is_email}},</span>
                    </div>
                    <el-button size="small" class="btn_pwd" type="primary" @click="get_email"></el-button>
                </div>

                <div v-if="is_phone" class="div">
                    <div class="img_box">
                        <img src="../../assets/img/phone.jpg" class="font_img">
                    </div>
                    <div class="text_box">
                        <h3 style="margin-left: -515px"></h3>
                        <span style="margin-left: -10px;font-size: 14px;">{{is_phone}},/</span>
                    </div>
                    <el-button size="small" class="btn_pwd" type="primary" @click="get_phone"></el-button>
                </div>

                <div class="div">
                    <div class="img_box">
                        <img src="../../assets/img/people.jpg" class="font_img">
                    </div>
                    <div class="text_box">
                        <h3 style="margin-left: -630px"></h3>
                        <p style="margin-left: -12px;font-size: 14px;text-align: left">
                            ,</p>
                    </div>
                    <el-button size="small" class="btn_pwd" type="primary" disabled></el-button>
                </div>
            </el-col>
        </el-row>

        <!--        -->
        <el-row v-if="email_pwd" style="margin-top: 33px">
            <h5>:{{is_email}}</h5>
            <el-col :span="6" :offset="9" style="margin-top: 30px">
                <el-input
                        placeholder=""
                        prefix-icon="el-icon-s-comment"
                        v-model="email"
                        clearable>
                </el-input>
                <br>
                <br>
                <br>
                <el-button style="width:100%" @click="send_email_pwd" type="success">
                </el-button>
            </el-col>
        </el-row>

        <!--        -->
        <el-row v-if="phone_pwd" style="margin-top: 33px">
            <h5>:{{is_phone}}</h5>
            <el-col :span="6" :offset="9" style="margin-top: 30px">
                <el-row>
                    <el-col :span="16">
                        <el-input
                                placeholder=""
                                prefix-icon="el-icon-phone"
                                v-model="phone"
                                clearable
                                @blur="check_mobile">
                        </el-input>
                    </el-col>
                    <el-col :span="8">
                        <el-button :disabled="is_click" type="success" size="medium" @click="get_phone_code">
                            {{sms_interval}}
                        </el-button>
                    </el-col>
                </el-row>
                <br>
                <br>
                <br>
                <el-input
                        placeholder=""
                        prefix-icon="el-icon-s-comment"
                        v-model="code"
                        clearable>
                </el-input>
                <br>
                <br>
                <br>
                <el-button style="width:100%" @click="send_phone_pwd" type="success"></el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>

    export default {
        name: "Method",
        data() {
            return {
                is_email: false,
                is_phone: false,
                is_method: true,
                email_pwd: false,
                phone_pwd: false,
                email: '',
                phone: '',
                is_click: true,
                is_send: false,
                sms_interval: '',
                code: '',
            }
        },
        created() {
            this.is_email = sessionStorage.email.replace(/(\w{1,3})(.+)(@.+)/g, "$1****$3")
            this.is_phone = sessionStorage.phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2');
        },
        methods: {
            get_email() {
                this.is_method = false;
                this.email_pwd = true
            },
            get_phone() {
                this.is_method = false;
                this.phone_pwd = true
            },

            // 
            send_email_pwd() {
                if (!this.email) {
                    this.$message({
                        message: '!',
                        type: 'warning',
                        duration: 1500
                    });
                    return false  // 
                }
                let regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if (!regEmail.test(this.email)) {
                    this.$message({
                        message: '',
                        type: 'error',
                        duration: 1500
                    })
                    this.email = ''
                    return false  // 
                }
                if (this.email !== sessionStorage.email) {
                    this.$message({
                        message: '!',
                        type: 'error',
                        duration: 2000
                    });
                    this.email = ''
                    return false  // 
                }
                this.$emit('success',)
                this.$http({
                    url: this.$settings.base_url + '/api/student/email_pwd/',
                    method: 'post',
                    data: {
                        email: this.email
                    }
                }).then(data => {
                        if (data.data.code === 200) {
                            // console.log('111')
                            this.$cookies.set('email_token', data.data.token, 60 * 5)
                            this.$message({
                                message: data.data.msg,
                                type: 'success',
                                duration: 2000
                            });
                            this.$EventBus.$emit('email', 'email');

                        } else if (data.data.code === 999) {
                            // console.log('222')
                            this.$message({
                                message: ',!',
                                type: 'warning',
                                duration: 1500
                            });
                            this.$router.push('/login')
                        } else {
                            // console.log('333')
                            this.$message({
                                message: data.data.msg,
                                type: 'warning',
                                duration: 1500
                            });
                        }
                    }
                )
            },

            // 
            check_mobile() {
                if (this.phone !== sessionStorage.phone) {
                    this.is_click = true
                    this.$message({
                        message: ',!',
                        type: 'error',
                        duration: 1500
                    });
                    this.phone = ''
                    return false
                }
                this.is_click = false
                this.email = ''
            }
            ,

            // 
            get_phone_code() {

                let sms_interval_time = 60;
                this.sms_interval = "...";
                let timer = setInterval(() => {
                    if (sms_interval_time <= 1) {
                        clearInterval(timer);
                        this.sms_interval = "";
                        this.is_click = false
                    } else {
                        sms_interval_time -= 1;
                        this.sms_interval = `${sms_interval_time}`;
                        this.is_click = true
                    }
                }, 1000);

                this.$http({
                    url: this.$settings.base_url + '/api/student/send_sms/',
                    method: 'post',
                    data: {
                        phone: this.phone
                    }
                }).then(data => {
                    if (data.data.code === 200) {
                        this.$message({
                            message: data.data.msg,
                            type: 'success',
                            duration: 1500
                        });
                    } else {
                        if (data.data.code === 999) {
                            this.$message({
                                message: ',!',
                                type: 'error',
                                duration: 1500
                            });
                            return false
                        } else {
                            this.$message({
                                message: data.data.msg,
                                type: 'error',
                                duration: 1500
                            });
                        }
                    }
                })
            }
            ,

            // 
            send_phone_pwd() {
                if (!this.code) {
                    this.$message({
                        message: '!',
                        type: 'warning',
                        duration: 1500
                    });
                    return false
                }
                this.$http({
                    url: this.$settings.base_url + '/api/student/phone_code/',
                    method: 'post',
                    data: {
                        phone: this.phone,
                        code: this.code
                    }
                }).then(data => {
                    if (data.data.code === 200) {
                        this.$message({
                            message: data.data.msg,
                            type: 'success',
                            duration: 1500
                        });
                        this.$emit('success',)
                    } else {
                        this.$message({
                            message: data.data.msg,
                            type: 'error',
                            duration: 1500
                        });
                    }
                })
            },
        }
    }
</script>

<style scoped>
    .div {
        height: 120px;
        width: 770px;
        margin-bottom: 16px;
        position: reLative;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
    }

    .img_box {
        position: absolute;
        left: 0;
        top: 10px;
    }

    .text_box {
        position: absolute;
        left: 140px;
        top: 8px;
    }

    p, span {
        color: #999999;
    }

    .font_img {
        height: 100px;
        width: 100px;
    }

    .btn_pwd {
        float: right;
    }
</style>