<template>
    <div class="pay-success">
        <!--()-->
        <Header/>
        <div class="main" style="margin-top: 40px">
            <div class="title">
                <div class="success-tips">
                    <p class="tips"> 1 </p>
                </div>
            </div>
            <div class="order-info">
                <p class="info"><b></b><span>{{ result.out_trade_no }}</span></p>
                <p class="info"><b></b><span>{{ result.trade_no }}</span></p>
                <p class="info"><b></b><span><span>{{ result.timestamp }}</span></span></p>
            </div>
            <div class="study">
                <span @click="go_study"></span>
            </div>
        </div>
    </div>
</template>

<script>
    import Header from "../../components/Home/Header"

    export default {
        name: "index",
        data() {
            return {
                url: '',
                error: '',
                result: {},
            };
        },
        created() {
            // url? => ?a=1&b=2
            // console.log(location.search);

            // url
            let params = location.search.substring(1);  // ? => a=1&b=2
            let items = params.length ? params.split('&') : [];  // ['a=1', 'b=2']
            //args
            for (let i = 0; i < items.length; i++) {  // a=1b=2
                let k_v = items[i].split('=');  // ['a', '1']
                //
                if (k_v.length >= 2) {
                    // url
                    let k = decodeURIComponent(k_v[0]);
                    this.result[k] = decodeURIComponent(k_v[1]);
                    // url
                    // this.result[k_v[0]] = k_v[1];
                }
            }
            // 
            // console.log(this.result);

            // get
            this.$http({
                // url: 'http://110.40.189.224:8001' + '/api/exam/success/' + location.search,
                // url: 'http://101.91.213.1:8001' + '/api/exam/success/' + location.search,
                url: this.$settings.base_url + '/api/exam/success/' + location.search,
                method: 'get',
                headers: {
                    token: this.$cookies.get('token')
                }
            }).then(date => {
                if (date.data.code === 200) {
                    this.url = '/home/video/course_detail/1'
                } else {
                    this.error = date.data.msg
                }
            })
        },
        methods: {
            go_study() {
                if (this.url) {
                    this.$router.push(this.url)
                } else {
                    this.$message({
                        message: this.error,
                        type: 'error',
                        duration: 1500
                    });
                }
            }
        },
        components: {
            Header,
        }
    }
</script>

<style scoped>
    .main {
        padding: 60px 0;
        margin: 0 auto;
        width: 1200px;
        background: #fff;
    }

    .main .title {
        display: flex;
        -ms-flex-align: center;
        align-items: center;
        padding: 25px 40px;
        border-bottom: 1px solid #f2f2f2;
    }

    .main .title .success-tips {
        box-sizing: border-box;
    }

    .title img {
        vertical-align: middle;
        width: 60px;
        height: 60px;
        margin-right: 40px;
    }

    .title .success-tips {
        box-sizing: border-box;
    }

    .title .tips {
        font-size: 26px;
        color: #000;
    }


    .info span {
        color: #ec6730;
    }

    .order-info {
        padding: 25px 48px;
        padding-bottom: 15px;
        border-bottom: 1px solid #f2f2f2;
    }

    .order-info p {
        display: -ms-flexbox;
        display: flex;
        margin-bottom: 10px;
        font-size: 16px;
    }

    .order-info p b {
        font-weight: 400;
        color: #9d9d9d;
        white-space: nowrap;
    }

    .study {
        padding: 25px 40px;
    }

    .study span {
        display: block;
        width: 140px;
        height: 42px;
        text-align: center;
        line-height: 42px;
        cursor: pointer;
        background: #ffc210;
        border-radius: 6px;
        font-size: 16px;
        color: #fff;
    }
</style>